	import moveVerify from "@/components/moveVerify.vue"

	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		components: {
		        "move-verify":moveVerify
		},
		
		data(){
			return {
				mobile: '',
				password: '',
				resultData:{},
				verifyCode: '',
				logining: false
			}
		},
		onLoad(){
			console.log('login页面onLoad');
		},
		methods: {
			...mapMutations(['login']),
			/* 校验结果回调函数 */
			verifyResult(res){
				console.log(res);
				this.resultData = res;
			},
			/* 校验插件重置 */
			verifyReset(){
				this.$refs.verifyElement.reset();

				/* 删除当前页面的数据 */
				this.resultData = {};
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack({
					delta: 1
				});
			},
			toRegist(){
				this.$api.msg('去注册');
				uni.navigateTo({
					url: '/pages/public/register',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toLoginByName(){
				uni.navigateTo({
					url: '/pages/public/loginByName',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toForgetPwd(){
				uni.navigateTo({
					url: '/pages/public/forgetPwd',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			async toLogin(){
				this.logining = true;
				let loginPhone = this.loginPhone;
				let loginPassword = this.loginPassword;
				const {mobile, password} = this;
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/
				const sendData = {
					mobile,
					password
				};
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
                    uni.navigateBack();
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		},

	}
